import { Button, Card, makeStyles } from "@material-ui/core";
import { useEffect, useState } from "react";
import RemoveIcon from "@mui/icons-material/Remove";
import AddIcon from "@mui/icons-material/Add";
import AddCard from "./AddCard";
import RemoveCard from "./RemoveCard";
import TabPages from "../TabPages";
import { useLocation } from "react-router-dom";
import { Grid } from "@mui/material";

const useStyles = makeStyles((theme) => ({
	card: {
		width: 500,
		maxWidth: 500,
		height: "100%",
		display: "flex",
		flexDirection: "column",
		justifyContent: "center",
		alignItems: "center",
		borderRadius: 15,
		boxShadow: `rgb(0 0 0 / 1%) 0px 0px 1px, rgb(0 0 0 / 4%) 0px 4px 8px, rgb(0 0 0 / 4%) 0px 16px 24px, rgb(0 0 0 / 1%) 0px 24px 32px`,
		backgroundColor: "#1f202c",
		paddingLeft: 20,
		paddingRight: 20,
		paddingTop: 20,
		paddingBottom: 20,
	},
	buttonsWrapper: {
		marginTop: 30,
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
	},
	icon: {
		color: "white",
		fontSize: 24,
	},
	button: {
		backgroundColor: "#4a4176",
		color: "white",
		borderColor: "white",
		height: 50,
		textTransform: "none",
		fontSize: 16,
		borderRadius: 10,
		marginLeft: 5,
		marginRight: 5,
		marginTop: 5,
		marginBottom: 5,
		padding: "20px 30px 20px 30px",
	},
	heading: {
		color: "white",
	},
}));

const AddLiquidity = () => {
	const classes = useStyles();
	const [showCard, setShowAdd] = useState({ status: false, component: "" });
	const query = new URLSearchParams(useLocation().search);

	const action = query.get("action");
	useEffect(() => {
		if (action === "add_liquidity") {
			setShowAdd({ status: true, component: "AddCard" });
		} else if (action === "remove_liquidity") {
			setShowAdd({ status: true, component: "RemoveCard" });
		}
	}, [action]);

	return (
		<>
			<TabPages data={1} />

			{!showCard.status ? (
				<Grid
					container
					sx={{ justifyContent: "center", margin: "3% 0%" }}
				>
					<Card elevation={10} className={classes.card}>
						<h4 className={classes.heading}>Pools</h4>

						<div className={classes.buttonsWrapper}>
							<Button
								className={classes.button}
								onClick={() =>
									setShowAdd({
										status: true,
										component: "AddCard",
									})
								}
							>
								<AddIcon className={classes.icon} /> Add
								liquidity
							</Button>
							<Button
								className={classes.button}
								onClick={() =>
									setShowAdd({
										status: true,
										component: "RemoveCard",
									})
								}
							>
								<RemoveIcon className={classes.icon} /> Remove
								liquidity
							</Button>
						</div>
					</Card>
				</Grid>
			) : showCard.component === "AddCard" ? (
				<AddCard
					handleBack={() =>
						setShowAdd({ status: false, component: "" })
					}
				/>
			) : (
				<RemoveCard
					handleBack={() =>
						setShowAdd({ status: false, component: "" })
					}
				/>
			)}
		</>
	);
};

export default AddLiquidity;
