	// const handleAddLiquidity = async () => {
	// 	if (
	// 		selectedToken0.symbol === NATIVE_TOKEN?.[chainId] ||
	// 		selectedToken1.symbol === NATIVE_TOKEN?.[chainId]
	// 	) {
	// 		let etherToken, erc20Token;
	// 		if (selectedToken0.symbol === NATIVE_TOKEN?.[chainId]) {
	// 			console.log("if");
	// 			etherToken = {
	// 				...selectedToken0,
	// 				amount: parsedToken1Value,
	// 			};

	// 			const _amount = toWei(
	// 				parsedToken2Value,
	// 				selectedToken1.decimals
	// 			);
	// 			erc20Token = {
	// 				...selectedToken1,
	// 				amount: _amount,
	// 			};
	// 			await dispatch(
	// 				addLiquidityEth({
	// 					ethToken: etherToken,
	// 					erc20Token: erc20Token,
	// 					account: account,
	// 					deadline: swapSettings.deadline,
	// 					chainId,
	// 				})
	// 			);
	// 		} else {
	// 			console.log("else");
	// 			const token0 = {
	// 				...selectedToken1,
	// 				amount: parsedToken2Value,
	// 			};

	// 			const _amount = toWei(
	// 				parsedToken1Value,
	// 				selectedToken0.decimals
	// 			);
	// 			const token1 = {
	// 				...selectedToken0,
	// 				amount: _amount,
	// 			};

	// 			await dispatch(
	// 				addLiquidity({
	// 					token0,
	// 					token1,
	// 					account,
	// 					deadline: swapSettings.deadline,
	// 					chainId,
	// 				})
	// 			);
	// 		}
	// 	} else {
	// 		console.log("final else");
	// 		const _amount0 = toWei(parsedToken1Value, selectedToken0.decimals);
	// 		const _amount1 = toWei(parsedToken2Value, selectedToken1.decimals);

	// 		const token0 = {
	// 			...selectedToken0,
	// 			amount: _amount0,
	// 		};

	// 		const token1 = {
	// 			...selectedToken1,
	// 			amount: _amount1,
